<template>
  <v-container>
    <v-row justify="center">
      <v-col lg="10" xl="7">
        <table-component
          class="text-center"
          :data="orderList"
          :show-filter="false"
          :show-caption="false"
        >
          <table-column show="row" label="Row number" :formatter="formatterRow"></table-column>
          <table-column show="table" label="Table" :formatter="formatterTable"></table-column>
          <table-column show="waiter" label="Waiter name" :formatter="formatterWaiter"></table-column>
          <table-column show="orderStatus" label="Status" :formatter="formatterStatus"></table-column>
          <table-column show="orderPrice" label="Order price" cell-class="tableTdRight" :formatter="formatterPrice"></table-column>
          <table-column show="orderCompletedTime" label="Order completed time" :formatter="formatterCompletedTime"></table-column>
          <table-column label="" :sortable="false" :filterable="false">
            <template slot-scope="row">
              <v-btn @click="showOrder(`${row.orderID}`)" small outlined color="indigo">bax</v-btn>
            </template>
          </table-column>
          <template slot="tfoot" slot-scope="{ rows }">
            <tr>
              <th>Order total:</th>
              <th>&nbsp;</th>
              <th>&nbsp;</th>
              <th>&nbsp;</th>
              <th class="tableTdRight"><span>{{orderListTotal}} AZN</span></th>
              <th>&nbsp;</th>
              <th>&nbsp;</th>
            </tr>
          </template>
        </table-component>
      </v-col>
      <!-- <p>{{new Date().toLocaleString('ru-RU')}}</p>-->
    </v-row>
  </v-container>
</template>

<script>
  export default {
    computed: {
      orderList() {
        return this.$store.getters.orderList
      },
      orderListTotal() {
        return this.$store.getters.orderListTotal
      }
    },
    methods: {
      formatterRow(val, rowProp) {
        if (rowProp.orderStatus === 'not completed') {
          return `<span class="tableNotFinished">${val}</span>`
        } else {
          return `<span>${val}</span>`
        }
      },
      formatterTable(val, rowProp) {
        if (rowProp.orderStatus === 'not completed') {
          return `<span class="tableNotFinished">${val}</span>`
        } else {
          return `<span>${val}</span>`
        }
      },
      formatterWaiter(val, rowProp) {
        if (rowProp.orderStatus === 'not completed') {
          return `<span class="tableNotFinished">${val}</span>`
        } else {
          return `<span>${val}</span>`
        }
      },
      formatterStatus(val, rowProp) {
        if (rowProp.orderStatus === 'not completed') {
          return `<span class="tableNotFinished">${val}</span>`
        } else {
          return `<span>${val}</span>`
        }
      },
      formatterPrice(val, rowProp) {
        if (rowProp.orderStatus === 'not completed') {
          return `<span class="tableNotFinished">${val}</span> <span class="tableNotFinished">AZN</span>`
        } else {
          return `<span>${val}</span> <span>AZN</span>`
        }
      },
      formatterCompletedTime(val, rowProp) {
        if (rowProp.orderStatus === 'not completed') {
          return `<span class="tableNotFinished">${val}</span>`
        } else {
          return `<span>${val}</span>`
        }
      },
      showOrder(id) {
        this.$router.push({path: `/order/${id}`})
      }
    },
    data: () => ({
      headers: [
        {
          text: 'Order ID',
          align: 'left',
          sortable: false,
          value: 'orderID',
        },
        {text: 'Table', value: 'tableID'},
        {text: 'Waiter name', value: 'waiter'},
        {text: 'Status', value: 'orderStatus'},
        {text: 'Order price', value: 'orderPrice', align: 'right',},
        {text: 'Order time', value: 'orderTime'},
      ],
    })
  };
</script>

<style>
  .table-component__table {
    width: 100%;
    border: 1px solid #ebebeb;
    border-collapse: collapse;
  }
  .table-component__table tr {
    border-bottom: 1px solid #ebebeb;
    background: #fff;
  }
  .table-component__table th, .table-component__table td { padding: .5rem }
  .tableTdRight { text-align: right }
  .tableNotFinished { color: red }
</style>
